.App {
  width: 100%;
  height: 100%;
  transition: 0.3s;
  overflow: hidden;
  background: #f2a969;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  height: 100vh;
}

.App.ON {
  background: #f37070;
}

.App.ON .Header {
  box-shadow: 1px 0px 25px #ca5b5b;
}

.Header {
  transition: 0.3s;
  width: 100wh;
  height: 50px;
  box-shadow: 1px 0px 25px #ce8f58;
  top: 0px;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

#timer_icon {
  height: 60px;
  width: 60px;
}

#header_icon {
  width: 30px;
  height: 30px;
}
.icon {
  margin: 10px;
}

@media screen and (max-width: 800px) {
  .Header {
    width: 100%;
    height: 50px;
    padding: 10px 1px;
  }

  #timer_icon {
    height: 40px;
    width: 40px;
  }
  .icon {
    margin: 15px 5px;
  }
}
